.sect_nav {
	padding: rem(24) 0;

	ul {

		li {
			
			margin-right: 0;

			a {
				font-size: rem(18);
			}
		}
	}
}

.page_questions {

	.sect_nav {

		ul {

			li {
				width: calc((100% - 2rem) / 3);
				&:nth-child(3) {
					order: unset;
					display: flex;
					justify-content: flex-end;
				}

				&:first-child {
					order: unset;
				}

				&:nth-child(2) {
					order: unset;
					display: flex;
					justify-content: center;
				}

				&:nth-child(4) {
					order: unset;
				}

				&:nth-child(5) {
					order: unset;
					display: flex;
					justify-content: center;

					a {
						margin-left: rem(40);
					}
				}

				&:last-child {
					order: unset;
					display: flex;
					justify-content: flex-end
				}

				
			}
		}
	}

	

	&__sect {
		padding: 0 0 rem(72) 0;

		&:first-child {
			padding: rem(72) 0;
		}

		>.title {
			margin-bottom: rem(56);
		}

		.item {
			h3 {
				font-size: rem(24);
			}
		}
	}
}

.feedback_faq {
	.field_recaptcha{
		display: none;
	}
}