.page_article {


	.article {
		h1 {
			font-size: rem(42);
		}

		p {
			font-size: rem(15);
		}

		&__share {
			h3 {
				font-size: rem(15);
			}
		}
	}

	.aside {
		> h3 {
			margin-top: rem(72);
			margin-bottom: rem(38);
		}

		.swiper-nav {
			height: rem(28);
			margin-top: rem(42);
		}

		.button_primary {
			padding: rem(16) rem(40);
			height: rem(56);
		}
	}

	.subscribe__form {
		margin-top: rem(72);
	}

	.products__items {
		margin-top: rem(72);
		padding: 0 rem(32);
		.swiper-nav {
			display: none;
		}
	}

}