.popup_overlay {
	&__wrap {
		width: rem(464);
		margin: 0 auto;

		h3 {
			font-size: rem(24);
			max-width: rem(429);
			margin-left: auto;
			margin-right: auto;
			text-align: center;
		}

		> p {
			max-width: rem(317);
		}
	}
}

.popup_modify {
	.popup_overlay__wrap {
		.items {

			.item {
				width: calc((100% - 8.7rem) / 2);
				margin-right: rem(28);
				margin-bottom: rem(40);
			}
		}
	}
}

.popup_review {
	.popup_overlay__wrap {
		width: rem(465) !important;
		display: block;


		.popup_header {
			background-color: var(--c-light-blue);
			padding: rem(14) rem(28);

			display: flex;
			flex-direction: column;
			align-items: center;

			span {}

			p {
				text-align: center;
				font-size: rem(15);
			}
		}
	}

	>.text {
		width: rem(465);
		margin-left: auto;
		margin-right: auto;
	}

	.field_file {
		p {
			max-width: 100% !important;
		}
	}
}