#header_goods_about {
    .header__top {
        margin-left: 66px;
        margin-right: 66px;
        margin-top: 0;
    }
    .header__bottom {
        margin-top: rem(40);
        &-left {
            height: rem(743);
            background: url(../img/page_goods_about/bg_gooda.jpg) no-repeat top center;
            background-size: cover;
            padding-bottom: rem(138);
            padding-top: rem(20);
            .swiper-overlay {
                width: rem(591);
                height: rem(591);
                background-color: #F7FAFF;
                .swiper-container {
                    padding-bottom: rem(70);
                    margin-top: rem(125);
                    // height: rem(250);
                }
            }
            .swiper-nav {
                bottom: rem(10);
                margin-left: rem(-64);
            }
        }
        &-right {
            margin: rem(72) 0;
            >span {
                font-size: rem(13);
            }
            h3 {
                font-size: rem(44);
            }
            p {
                margin-bottom: rem(16);
            }
            .icons {
                margin-right: rem(20);
                i {
                    font-size: rem(25);
                }
            }
            .count {
                font-size: rem(14);
            }
            a.button_primary {
                order: 7;
                margin-top: 0;
                margin-bottom: 0;
                align-self: flex-start;
            }
            ul {
                order: 5;
                margin-top: rem(48);
                margin-bottom: rem(56);
                li {
                    font-size: rem(15);
                }
            }
            .all {
                order: 6;
                margin-bottom: rem(46);
                font-size: rem(15);
            }
        }
    }
}

.slide_one {
    .swiper-container {
        margin-top: rem(60) !important;
        .swiper-nav {
            display: none;
        }
    }
}