 #header_faq {
	 height: rem(524);
	 background: url(../img/page_faq/header_bg_x2.jpg) no-repeat top center;
	 background-size: cover;

	.header__bottom {
		margin-top: rem(72);

		h1 {
			margin-bottom: rem(24);
		}
		p {
			font-family: $font-mplus;
			margin-bottom: rem(36);
			font-size: rem(20);
			max-width: rem(346);
			color: var(--c-paragraph);
		}

		.search_form {
			width: rem(490);
			margin-bottom: rem(34);

			input {
				height: rem(65);
				font-size: rem(18);
				font-weight: 500;
				// margin-bottom: rem(34);
			}

			button {
				top: rem(17);
			}
		}

		a {
			font-size: rem(16);
		}
	}
 }