.page_research {
    .sect_nav {
        border-bottom: 0.0625rem solid #E5E8EC;
        margin-bottom: rem(56);
    }
    .articles__items {
        .item {
            border-bottom: 0;
        }
    }
    &__table {
        padding-left: rem(84);
        padding-right: rem(84);
        .header_links {
            display: none;
        }
    }
}

.page_research.position_scroll {
    &::before {
        height: rem(68);
    }
}

.wrap_links {
    margin-bottom: rem(72);
    ul {
        li {
            a {
                &::after {
                    // content: "\e90d";
                    // font-family: 'icons';
                    color: var(--c-accent);
                    // font-size: rem(22);
                }
            }
        }
    }
}