.questions {
    padding: rem(72) 0;
    &__left {
        margin-bottom: rem(56);
        h3 {
            font-size: rem(36);
            max-width: rem(430);
        }
    }
    &__right {
        .accordion {
            &__item {
                .title {
                    span {
                        font-size: rem(15);
                        max-width: 100%;
                    }
                }
            }
        }
    }
}