.table_col {
    padding: rem(84) 0;
    &__items {
        justify-content: center;
        .item {
            width: rem(125);
            margin-bottom: 0;
            margin-right: rem(20);
            &:last-child {
                margin-right: 0;
            }
            p {
                font-size: rem(15);
                max-width: 100%;
            }
        }
    }
}