.numbers {
    &__top {
        position: relative;
        // height: rem(430);
    }
    &__bottom {
        top: rem(-58);
        background: url(../img/section_numbers_bg_x1.jpg) no-repeat top center;
        background-size: cover;
        h2 {
            // font-size: rem(34);
            max-width: rem(482);
            text-align: center;
            margin-left: auto;
            margin-right: auto;
            margin-bottom: rem(20);
        }
        p.text {
            font-size: rem(18);
            max-width: rem(544);
            text-align: center;
            margin-left: auto;
            margin-right: auto;
            margin-bottom: rem(56);
        }
        &-items {
            flex-wrap: wrap;
            flex-direction: row;
            justify-content: space-between;
            padding-left: rem(120);
            padding-right: rem(120);
            .item {
                flex: 0 0 35%;
                padding-left: 0;
                margin-bottom: rem(40);
                &:nth-child(3) {
                    margin-bottom: 0;
                }
                &__title {
                    font-size: rem(25);
                    line-height: 130%;
                    &::before {
                        display: none;
                    }
                }
                &__text {
                    font-size: rem(15);
                }
            }
        }
    }
}