.sertifications_accordion {
	padding: rem(72) 0;

	&__left {
		h2 {
			display: block;
			font-size: rem(34);
			font-weight: 500;
			padding-bottom: rem(62);
			border-bottom: rem(1) solid var(--c-dark);
			letter-spacing: rem(0.5);
		}

		
	}
}


.accordion {
	&__item {
		.title {
			span {
				font-size: rem(15);
			}
		}

		.text {

			p {
				font-size: rem(15);
			}
		}
	}
}