.review {
    padding: rem(74) 0;
    &__top {
        margin-bottom: rem(56);
        h2 {
            margin-bottom: rem(36);
        }
        p {
            max-width: 100%;
        }
    }
    &__bottom {
        margin-left: rem(90);
        margin-right: rem(90);
        .button_secondary {
            padding: rem(16) rem(26);
        }
        .swiper-wrapper {
            margin-bottom: rem(40);
        }
    }
    &__item {
        padding: rem(28) rem(20);
        &-top {
            p {
                font-size: rem(15);
            }
        }
        &-bottom {
            >.flex {
                margin-bottom: rem(7);
                align-items: center;
            }
        }
    }
}

.user_info {
    align-items: flex-end;
    &__text {
        .title {
            font-size: rem(14);
        }
    }
}

.block_icon {
    p {
        font-size: rem(15);
    }
}

.swiper-nav {
    height: rem(40);
    &-prev,
    &-next {
        // width: rem(67);
        i {
            font-size: rem(38);
            &::before {
                transition: all 200ms ease;
            }
        }
        // &:hover {
        // 	i {
        // 		font-size: rem(40);
        // 	}
        // }
        &:hover {
            i {
                &::before {
                    color: black;
                }
            }
        }
    }
    &-prev {
        margin-right: rem(38);
    }
    .swiper-button-disabled {
        i {
            font-size: rem(22);
        }
    }
}