.reviews_col {
	background-color: var(--c-accent);
	padding: rem(72) rem(82);
	background-image: none;
	// background-size: cover;

	>.container {
		display: flex;
		flex-direction: column;
	}

	&__left {
		// width: rem(400);
		margin-bottom: rem(34);

		h3 {
			font-family: $font-merri;
			font-size: rem(36);
			line-height: 150%;
			font-weight: normal;
			margin-bottom: rem(56);

			text-align: center;
		}

		.links {

			a {
				font-size: rem(15);
			}
		}
	}

	&__right {
		// margin-left: rem(30);

		.swiper-nav {
			margin-top: rem(22);
			height: rem(38);
		}
	}
}